.container {
  max-width: 1312px;
  padding: 0 10px;
  margin: 0 auto;
}

@media (min-width: 992px) {
  .container {
    padding: 0 20px;
  }
}
.header__wrapper {
  padding-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__mob-wrap {
  display: flex;
  align-items: center;
  gap: 30px;
}

.logo {
  display: block;
  position: relative;
  z-index: 3;
  max-width: 47px;
}
.logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

@media (min-width: 992px) {
  .header__wrapper {
    padding-bottom: 20px;
  }
  .header__mob-wrap {
    flex: 1 1 auto;
  }
  .logo {
    max-width: 96px;
    flex: 0 0 96px;
  }
}
.header__nav {
  padding-top: 113px;
  border-radius: 0px 0px 0px 5px;
  box-shadow: 0px 4px 4px 0px rgb(15, 19, 36);
  background: rgb(49, 56, 85);
  position: absolute;
  z-index: 2;
  top: 0px;
  left: -100%;
  transition: 0.3s ease;
  width: 100%;
  justify-content: center;
  height: 100svh;
  overflow: auto;
}
.header__nav::before {
  content: "DEFINIT TECHNOLOGIES";
  position: absolute;
  top: 35px;
  right: 100px;
  color: rgb(255, 255, 255);
  font-family: Cabin;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  letter-spacing: 30%;
  text-align: center;
}

.header__nav-list {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 33px;
}

.header__nav-list-item a {
  color: rgb(245, 245, 245);
  font-family: Nunito;
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0%;
  text-align: center;
}

.nav__reels a {
  border: 1px solid rgb(255, 255, 255);
  border-radius: 20px;
  padding: 5px 11px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.header__nav-active {
  display: flex;
  left: 0;
  transition: 0.3s ease;
}

@media (min-width: 992px) {
  .header__nav {
    height: initial;
    position: initial;
    background: initial;
    padding: initial;
    box-shadow: initial;
  }
  .header__nav::before {
    display: none;
  }
  .header__nav-list {
    flex-direction: initial;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
  }
}
.header__phone {
  display: none;
}

.header__phone-text {
  color: rgb(245, 245, 245);
  font-family: Nunito;
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;
}

.header__reels {
  cursor: pointer;
  background: linear-gradient(180deg, rgb(241, 233, 233), rgb(71, 86, 213) 33.13%, rgb(189, 97, 97) 73.597%, rgb(218, 194, 19) 100%);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__reels-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 43.75px;
  height: 43.75px;
  border-radius: 50%;
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25);
  background: rgb(49, 56, 85);
}
.header__reels-wrap img {
  max-width: 40.63px;
  width: 100%;
  height: 100%;
}

@media (min-width: 992px) {
  .header__reels {
    width: 80px;
    height: 80px;
    flex: 0 0 80px;
  }
  .header__reels-wrap {
    width: 70px;
    height: 70px;
  }
  .header__reels-wrap img {
    max-width: 65px;
  }
  .header__phone {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  .header__reels-phone-wrap {
    display: flex;
    align-items: center;
    gap: 37px;
  }
}
.burger {
  width: 30px;
  height: 20px;
  cursor: pointer;
  position: relative;
  z-index: 2;
}
.burger::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: 0.3s ease;
  border-radius: 5px;
}
.burger::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: 0.3s ease;
  border-radius: 5px;
}
.burger span {
  display: block;
  border-radius: 5px;
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  top: 8px;
  left: 0;
  transition: 0.3s ease;
}

.burger__active::after {
  transform: rotate(45deg);
  top: 8px;
}
.burger__active::before {
  transform: rotate(-45deg);
  bottom: 9px;
}
.burger__active span {
  opacity: 0;
}

@media (min-width: 992px) {
  .burger {
    display: none;
  }
}
.crumbs__nav {
  margin-top: 25px;
  margin-bottom: 50px;
}

.crumbs__nav-list {
  display: flex;
  align-items: center;
  gap: 5px;
}

.crumbs__nav-list-item a {
  color: rgb(245, 245, 245);
  font-family: Nunito;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
}
.crumbs__nav-list-item:last-child a {
  font-weight: 600;
}

.crumbs__nav-list-item-arrow {
  max-width: 10px;
  max-height: 15px;
}

@media (min-width: 992px) {
  .crumbs__nav {
    margin-top: 55px;
    margin-bottom: 90px;
  }
  .crumbs__nav-list {
    gap: 20px;
  }
  .crumbs__nav-list-item a {
    font-size: 20px;
    line-height: 27px;
  }
}
.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
}

.slick-slide {
  margin: 0 10px;
}

.slick-dots {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.slick-dots li button {
  font-size: 0;
  width: 10px;
  height: 10px;
  background: rgba(154, 162, 195, 0.3);
  border-radius: 50%;
}
.slick-dots .slick-active button {
  background: #fff;
}

.blog__list {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.blog__list-item {
  display: flex;
  flex-direction: column;
}

.blog__list-item-title {
  color: rgb(245, 245, 245);
  font-family: Nunito;
  font-size: 20px;
  font-weight: 600;
  line-height: 27px;
  margin-bottom: 30px;
}

.blog__list-item-slider-item {
  width: 307px;
}
.blog__list-item-slider-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 5px;
}

.blog__list-item-text {
  color: rgb(245, 245, 245);
  font-family: Nunito;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  margin-top: 30px;
}

.blog__list-item-text-full {
  color: rgb(245, 245, 245);
  font-family: Nunito;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  cursor: pointer;
  display: inline-block;
  margin-top: 25px;
  align-self: flex-end;
}

.slick-slide img {
  transition: 0.3s ease;
  transform: scale(0.5);
}

.slick-center img {
  transform: scale(1);
}

.work__create-date {
  display: none;
}

@media (min-width: 992px) {
  .blog__list-item-title {
    font-size: 36px;
    line-height: 49px;
    margin-bottom: 50px;
  }
  .slick-slide {
    width: 704px;
  }
  .slick-slide img {
    transform: scale(0.75);
  }
  .work__create-date {
    display: inline-block;
    color: rgb(255, 255, 255);
    font-family: Nunito;
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
    margin-top: 10px;
    align-self: flex-end;
  }
  .slick-center img {
    transform: scale(1);
  }
  .slick-dots {
    margin-top: 50px;
  }
  .blog__list-item-text {
    margin-top: 50px;
    font-size: 20px;
    line-height: 27px;
    -webkit-line-clamp: initial;
  }
  .blog__list-item-text-full {
    display: none;
  }
}
